body {
  background: #f9f9f9;
  display: flex;
  font-family: Helvetica, Arial, sans-serif;
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  position: fixed;
}
input[type=range]{
  -webkit-appearance: none;
  border: none;
  border-radius: 20px;
  overflow: hidden;
  outline: none;
  width: 100%;

  &::-webkit-slider-runnable-track {
    width: 100%;
    height: 16px;
    background: white;
    border: none;
  }
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    height: 16px;
    width: 30px;
    border-radius: 20px;
    background: green;
  }
  &:focus {
    outline: none;
  }
}

